import React from 'react';

import styles from './powerAnimatedExpainerVideos.module.scss';

const title = 'Power of animated explainer videos';
const subTitle = 'Rally viewers around your brand using the power of animated explainer videos.';
const date = '26/05/2020';
const desc = 'Not using explainer videos as part of your online marketing strategy is already causing your company to fall behind your competition. But do not create just any video. Quality content builds trust and maintain brand awareness.';

const powerAnimatedExpainerVideos = () => {
  return (
    <div className={styles.container}>
      <h1>{title}</h1>
      <h2>{subTitle}</h2>
      <p className="date">{date}</p>
      {/* <div className="seperator" /> */}
      <p className="desc">{desc}</p>
      <div className="seperator mt-4 mb-4" />
      <iframe title="Explainer Video Marketing Statistic (2020)" src={`https://www.youtube.com/embed/C3Q9MhTD18g`} />
    </div>
  );
}

export default powerAnimatedExpainerVideos;
